@import url(https://cdn.jsdelivr.net/font-nanum/1.0/nanumgothic/nanumgothic.css);

.visuallyhidden{
    display: none;
}

.nanumgothic * {
 font-family: 'Nanum Gothic', sans-serif;
}

:root {
    --clr-dark: #0f172a;
    --clr-light: #f1f5f9;
    --clr-accent: #e11d48;
    --clr-upper: rgb(31, 29, 29);

}

body, html {
    font-family: 'Nanum Gothic';
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0; /* 추가: 기본 패딩 제거 */
}

.pages {
    display: flex;
    /* width: 100%;
    height: auto; */
    /* height: 100%; */
    display: flex;
    flex-direction: column;
    /* box-sizing: border-box; */
}

.page {
    width:100vw;
    height: 93vh;
    background-color: rgb(255, 255, 255);
    padding: 0;
    color: var(--clr-light);
    text-align: center;
    /* border: 15px solid var(--clr-accent); */
    box-sizing: border-box;
}

/* 첫번째 페이지 */
#page1 {
    height: 100vh;
    width: auto;
    margin-top: auto;
    /* filter:blur(3px); */
    /* opacity: 0.9; */
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    background-color:burlywood;
}

#page2{
    background-color: white;
}

#page3{
    background-color: #f26e19;
}


#page4{
    background-color: #235aac;
}

#page5{
    background-color: white;
    color:black;
}

.image-box {
    height: auto;
    width: 100%;

    display: absolute;
    /* background-image: url('https://wwwk.kangwon.ac.kr/DATA/bbs/21/20230831160609082rKE.jpg'); */
    background-size: cover;
    filter:brightness(66%);
    /* filter:blur(2px); */
}
@media(max-width:1400px){
    .image-box{
        height: 100%;
        width: auto;
    }
}

.text-box {
    text-align: center; /* 텍스트 가운데 정렬 */
    /* background-color: rgba(0, 0, 0, 0.8); */
    /* padding: 20px; */
    z-index: 1;
    position: absolute;
    /* font-size : 2em; */
    font-family: 'Lato';
    font-weight : 700;
}

.lab-name {
    font-size : 4em;
    margin-bottom : 1%;
}
/* .lab-text{

} */
.univ-name {
    font-size : 1.5em;
}   

.up{
    position: fixed;
    top: 95vh;
    left:93vw;
    color:rgba(0,0,0,0);
    text-decoration: none;
}

.upperbox {
    width: 100%;
    height: 7vh;
    background-color: rgba(255, 255, 255, 0);
    padding: 0;
    color: white;
    text-align: left;
    /* border: 15px solid var(--clr-upper); */
    /* opacity: 0; */
    box-sizing: border-box;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    box-shadow: 0px 3px 5px -3px grey;;
}

.upperbox-items{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 100%
}

.left {
    height: 100%;
    box-sizing: border-box;    
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.logo-text {
    display: flex;
    flex-direction: row;
    height: 100%;
    box-sizing: border-box;
    align-items: center;
}

.menus {
    height: 100%;
    /* border: 3px solid var(--clr-dark); */
    box-sizing: border-box;    
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.menuitem {
    box-sizing: border-box;
    align-items: center;
    font-family: sans-serif;
    padding-right: 10px;
    cursor:pointer;
}

.logo img {
    
    width: auto; /* 이미지 너비 조절 예시 */
    height: 80%; 
    /* margin-bottom: 10%; */
    margin-right: 10px;
    /* border: 2px solid #ccc;  */
    cursor: pointer;
}

.whitebox{
    width:70vh;
    height: 70vh;
}
#wb1{
    padding-left: 5vw;
}
#wb2{
    padding-right: 5vw;
}

.imgbox{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin:5vw;
    margin-left:10vw;
}
#professor-img{
    width:24vw;
    height:32vw;
    border-radius: 2vw;
    border-width: 1vw;
    border-style: solid;
    border-color:white;
}

.page.pc{
    height: 70vh;
}
.introduce{
    display: flex;
    flex-direction: column;
    height: 140vh;
}
.writting{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.inner{
    font-family: sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.inner-title{
    text-align: center;
    font-size: 5vh;
}
.inner-writting{
    padding-left:5vw;
    padding-right: 5vw;
    padding-top:5vh;
    font-size:larger;
}

.copyright{
    margin:5vw;
}



@media(max-width:500px){

    .whitebox{
        padding-left: 0px;
        width:100%;
        display: absolute;
        z-index: 1;
    }
    .page.pc{
        display: none;
    }
    #page3{
        z-index: -1;
    }
}

.divider .out{
    height:10vh;
    width: 100vw;
    background-color:white;
    display:block;
}
.divider .in{
    height: 2px;
    width: 80vw;
    margin-left: 10vw;
    margin-right: 10vw;
    background-color: rgba(160, 149, 149, 0.46);
}

.member-page{
    background-color: white;
    display: flex;
    flex-direction: column;
}

.member-title{
    font-size:5vh;
    font-weight: bold;

    margin-top:10vh;
    margin-left:25vw;
}

.card-placeholder{
    margin: 8vw;
    margin-top: 5vh;
    margin-bottom: 5vh;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.member-card{
    display: flex;
    flex-direction: row;
    justify-content:left;
    width:40vw;
    height: 40vh;
    padding-left:0.5vw;
    background-color: rgba(238, 250, 250,0);
    border-radius: 20px;
    margin-right: 1vw;
    margin-bottom: 2vw
}

/* #professor-card{
    width:80vw;
} */

.member-card-photoplace{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.member-card-photo{
    width: 12vw;
    height: 16vw;
    border-radius: 5px;
    margin-left:3vw;
    margin-right: 3vw;
}

.member-card-introduce{
    display: flex;
    flex-direction: column;
    justify-content:center;
    margin-left:1vw
}

.member-card-introduce-name{
    font-size:3vh;
}

.member-card-introduce-curriculum{
    font-size: 2vh;
    color:gray;
}

.member-card-introduce-other{
    font-size: 2vh;
    padding-bottom:6px;
}

.member-card-introduce-another{
    font-size: 3vh;
    padding-bottom:4px;
}

.member-card-introduce-another.in{
    font-size: 2vh;
}


#member-card-introduce-professor{
    margin-left:10vw;
    display:none;
}

.link{
    color: black;
}

.major{
    font-size:2.4vh;
    margin-bottom:1vh;
}